import React, { useState, forwardRef } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

interface CustomDatePickerProps {
  value: string;
  onChange: (date: string) => void;
  placeholder?: string;
  className?: string;
  disabled?: boolean;
}

/**
 * A premium custom date picker component
 */
const CustomDatePicker: React.FC<CustomDatePickerProps> = ({
  value,
  onChange,
  placeholder = "dd/mm/yyyy",
  className = "",
  disabled = false,
}) => {
  // Convert string to Date object for DatePicker
  const stringToDate = (dateString: string): Date | null => {
    if (!dateString) return null;
    const parts = dateString.split('-');
    if (parts.length === 3) {
      return new Date(
        parseInt(parts[0], 10),
        parseInt(parts[1], 10) - 1,
        parseInt(parts[2], 10)
      );
    }
    return null;
  };

  // Convert Date to string for form state
  const dateToString = (date: Date | null): string => {
    if (!date) return '';
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  };

  // Create date state from incoming value
  const selectedDate = stringToDate(value);

  // Custom input to maintain styling consistency
  const CustomInput = forwardRef<HTMLDivElement, { value?: string; onClick?: () => void }>(
    ({ value, onClick }, ref) => (
      <div 
        className={`relative w-full h-10 px-3 border border-[#E6EAF2] rounded focus-within:ring-1 focus-within:ring-[#0B498B] flex items-center ${className} ${
          disabled ? "bg-gray-100" : "bg-white"
        }`} 
        ref={ref}
      >
        <input
          type="text"
          value={value || ''}
          readOnly
          placeholder={placeholder}
          onClick={onClick}
          className={`w-full h-full text-sm outline-none bg-transparent cursor-pointer ${
            value ? "text-[#1C1C1C]" : "text-[#A0A0A0]"
          }`}
          disabled={disabled}
        />
        <div className="absolute right-3 top-1/2 transform -translate-y-1/2 pointer-events-none">
          <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M12.6667 2.66675H3.33333C2.59695 2.66675 2 3.2637 2 4.00008V13.3334C2 14.0698 2.59695 14.6667 3.33333 14.6667H12.6667C13.403 14.6667 14 14.0698 14 13.3334V4.00008C14 3.2637 13.403 2.66675 12.6667 2.66675Z" stroke="#A0A0A0" strokeWidth="1.33333" strokeLinecap="round" strokeLinejoin="round"/>
            <path d="M10.6667 1.33325V3.99992" stroke="#A0A0A0" strokeWidth="1.33333" strokeLinecap="round" strokeLinejoin="round"/>
            <path d="M5.33325 1.33325V3.99992" stroke="#A0A0A0" strokeWidth="1.33333" strokeLinecap="round" strokeLinejoin="round"/>
            <path d="M2 6.66675H14" stroke="#A0A0A0" strokeWidth="1.33333" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </div>
      </div>
    )
  );

  const handleChange = (date: Date | null) => {
    onChange(dateToString(date));
  };

  return (
    <DatePicker
      selected={selectedDate}
      onChange={handleChange}
      customInput={<CustomInput />}
      dateFormat="dd/MM/yyyy"
      showPopperArrow={false}
      disabled={disabled}
      calendarClassName="custom-datepicker"
      popperClassName="custom-datepicker-popper"
      popperModifiers={[
        {
          name: 'offset',
          options: {
            offset: [0, 8],
          },
        },
        {
          name: 'preventOverflow',
          options: {
            rootBoundary: 'viewport',
            padding: 8,
          },
        },
      ]}
      dayClassName={date => 
        date.getDate() === new Date().getDate() &&
        date.getMonth() === new Date().getMonth() &&
        date.getFullYear() === new Date().getFullYear()
          ? "today-date"
          : undefined
      }
    />
  );
};

export default CustomDatePicker; 